









import { Component, Vue } from 'vue-property-decorator';
import { asteroids } from '@/game/Asteroids';

@Component({
  components: {}
})
export default class Game extends Vue {
    mounted() {
        setTimeout(() => {
            asteroids(this.$refs.game as HTMLElement);
        }, 100);
    }

    destroyed() {
        console.log(this, 'destroyed')
    }
}
